// React Package Module
import { useMemo, useState, useRef, useEffect } from "react";

// MUI Package Module
import { Box, styled } from "@mui/material";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { MESSAGE, LABEL, CODE } from "../../common";

// TextField Component
import TextField from "@mui/material/TextField";

// API Service
import { patchData } from "common/communication";

// Util
import { pwValidation } from "common/util";

/** STYLE ì •ì˜ */
// ì „ì²´ ì˜ì—­
const Wrapper = styled(Box)({
  justifyContent: "center",
  alignItems: "center",
  padding: "0px 20px 20px 20px"
});

const Text = styled(TextField)({
  width: "100%",
  "& .MuiInputBase-input": {
    height: 20,
    fontSize: 14,
    marginTop: 8
  },
});

const ButtonBox = styled(Box)({
  width: "100%",
  display: 'flex',
  justifyContent: 'flex-end',
});

const fileUploaderBtn =  {
  backgroundColor: "white",
  color: "black",
  border: "none", 
  cursor: "pointer",
  display: "block",
  margin: "0 auto",
  fontSize: 15
};

/** ë‚´ ì •ë³´ ìˆ˜ì • ì»´í¬ë„ŒíŠ¸ (í—¤ë”ì˜ ë‚´ ì •ë³´ ë²„íŠ¼ í´ë¦­ ì‹œ í•´ë‹¹ íŒì—… ë„ì›Œì¤Œ)*/
const EditProfile = (props) => {
  // êµ¬ì¡° ë¶„í•´ í• ë‹¹ ì´ìš©í•˜ì—¬ props ë¶„í•´
  const { closeModal } = props;
  
  // STATE ì •ì˜
  const [profile, setProfile] = useState(JSON.parse(localStorage.getItem("profile")));	// ìœ ì € ì •ë³´
  const [newPw, setNewPw] = useState(""); 												// ìƒˆ ë¹„ë°€ë²ˆí˜¸
  const [curPw, setCurPw] = useState(""); 												// í˜„ì¬ ë¹„ë°€ë²ˆí˜¸
  const [nickname, setNickname] = useState(profile.user_nickname); 						// ë¡œì»¬ìŠ¤í† ë¦¬ì§€ì— ì €ì¥ëœ ì‚¬ìš©ì ë‹‰ë„¤ì„
  const [img, setImg] = useState(); 													// ì‚¬ìš©ì í”„ë¡œí•„ ì´ë¯¸ì§€
  const [confirmNewPwRegMsg, setConfirmNewPwRegMsg] = useState(""); 					// ë¹„ë°€ë²ˆí˜¸ ë³€ê²½ ì‹œ ìƒˆ ë¹„ë°€ë²ˆí˜¸ì™€ ìƒˆ ë¹„ë°€ë²ˆí˜¸ í™•ì¸ì˜ ê°’ì´ ë‹¤ë¥¼ ê²½ìš° ë„ì›Œì£¼ëŠ” ì—ëŸ¬ ë©”ì„¸ì§€
  const [pwRegMsg, setPwRegMsg] = useState(""); 										// ë¹„ë°€ë²ˆí˜¸ ìœ íš¨ì„± ê²€ì‚¬ ë¯¸í†µê³¼ ì‹œ ë„ì›Œì£¼ëŠ” ì—ëŸ¬ ë©”ì„¸ì§€
  const [nickNameRegMsg, setNickNameRegMsg] = useState(""); 							// ë‹‰ë„¤ì„ ìœ íš¨ì„± ê²€ì‚¬ ë¯¸í†µê³¼ ì‹œ ë„ì›Œì£¼ëŠ” ì—ëŸ¬ ë©”ì„¸ì§€
  const [info, setInfo] = useState(""); 												// ì‘ê°€ ì†Œê°œ
  const [isAuthor, setIsAuthor] = useState(
    profile.user_reg_dv === "W" ? true : false
  );
  const [selectedFileName, setSelectedFileName] = useState("");   // ì‚¬ìš©ìê°€ ì„ íƒí•œ í”„ë¡œí•„ ì‚¬ì§„ ì´ë¦„
  const [previewUrl, setPreviewUrl] = useState("");

  const imageUrl = useMemo(() => {
	if (previewUrl !== "") {
		return previewUrl;
	} else {
		return `${process.env.REACT_APP_IMAGE_DIRECTORY}/${profile.image}?${Date.now()}`;
	}
  }, [profile.image, previewUrl]);

  /** Input ì…ë ¥ */
  // ë‹‰ë„¤ì„
  const inputNickname = (e) => {
    setNickname(e.target.value);
  };
  // í˜„ì¬ ë¹„ë°€ë²ˆí˜¸
  const inputCurPw = (e) => {
    setCurPw(e.target.value);
  };
  // ìƒˆ ë¹„ë°€ë²ˆí˜¸
  const inputNewPw = (e) => {
    setNewPw(e.target.value);
  };
  // ì‘ê°€ ì†Œê°œ
  const inputInfo = (e) => {
    setInfo(e.target.value);
  };

  // ë‹‰ë„¤ì„ ê¸€ììˆ˜ ì œí•œ ìœ íš¨ì„± ê²€ì‚¬
  const nickNameValidation = /^.{1,12}$/;

  // ë‹‰ë„¤ì„ ìœ íš¨ì„± ê²€ì‚¬
  const nickNameValidate = (e) => {
    if (!nickNameValidation.test(e.target.value)) {
      setNickNameRegMsg(MESSAGE.ERROR.NICKNAME_INVALIDATION);
    } else {
      setNickNameRegMsg("");
    }
  };

  // ë¹„ë°€ë²ˆí˜¸ í™•ì¸ ìœ íš¨ì„± ê²€ì‚¬
  const confirmNewPassword = (e) => {
    if (e.target.value !== newPw) {
      setConfirmNewPwRegMsg(MESSAGE.ERROR.CONFIRM_NEW_PW);
    } else {
      setConfirmNewPwRegMsg("");
    }
  };

  const validation = () => {
    setPwRegMsg(pwValidation(newPw));
  };

  // í”„ë¡œí•„ ìˆ˜ì •
  const onEditProfile = () => {
		// í˜„ì¬ ë¹„ë°€ë²ˆí˜¸ ë¯¸ì…ë ¥ ì‹œ
		if (curPw === "") {
			alert(MESSAGE.WRITE_CUR_PW);
			return;
		} else if (info.length > 50) {
      alert(MESSAGE.ERROR.INFO_INVALIDATION);
      return;
    }

		// ì´ë¯¸ì§€ íŒŒì¼ì˜ ê²½ìš° jsonì— ê·¸ëƒ¥ ë‹´ëŠ”ê²Œ ì•„ë‹ˆë¼ formData í˜•íƒœë¡œ ë§Œë“¤ì–´ì¤˜ì•¼ í•˜ë‹ˆ formDataë¥¼ ì„ ì–¸í•˜ê³  ê·¸ í¼ë°ì´í„°ì•ˆì— ë°ì´í„°ì™€ ì´ë¯¸ì§€ íŒŒì¼ ì •ë³´ë¥¼ ë„£ì–´ì„œ ì‚¬ìš©
		const data = {
			login_id: profile.login_id,
			user_nickname: nickname,
			current_pw: curPw,
			new_pw: newPw,
			file: img,
			author_info: info,
			isAuthor: isAuthor,
			old_img_name: profile.image,
		};

		// í¼ ë°ì´í„° ë³€ìˆ˜ ì„ ì–¸
		const formData = new FormData();

		// dataì˜ ê° keyê°’ìœ¼ë¡œ formdataì— ë°ì´í„° ì„¸íŒ…
		Object.keys(data).forEach((key) => {
			formData.append(key, data[key]);
		});
			patchData("auth/editProfile", formData).then((data) => {
				if (typeof data === "object") {
					const newData = JSON.stringify({
						...profile,
						user_nickname: data.user_nickname,
						image: data.image,
						author_info: data.author_info,
					});
					localStorage.setItem("profile", newData);
					setProfile(newData);
					alert(MESSAGE.EDIT_SUCCEED);
					closeModal();
				} else {
					alert(data);
				}
			});
	};

  // íŒŒì¼ ì—…ë¡œë“œ í•˜ëŠ” inputì— ëŒ€í•œ ì°¸ì¡°ë¥¼ ì €ì¥í•˜ëŠ” ë³€ìˆ˜ (í˜„ì¬ ì°¸ì¡° ëŒ€ìƒì´ ì—†ìŒì„ ë‚˜íƒ€ë‚´ê¸° ìœ„í•´ ì´ˆê¸°ê°’ nullë¡œ ì„¸íŒ…)
  const fileInputRef = useRef(null);

  // íŒŒì¼ ì—…ë¡œë“œ í•˜ëŠ” input í´ë¦­ í–ˆì„ ë•Œ ê¸°ëŠ¥ í•¨ìˆ˜ (í˜„ì¬ ì°¸ì¡°í•˜ëŠ” ìš”ì†Œ í´ë¦­í•˜ë„ë¡)
  const fileUploadBtn = () => {
    fileInputRef.current.click();
  }

  const fileChange = (e) => {
	const file = e.target.files[0];
	setImg(file);
    setSelectedFileName(file.name); // ì„ íƒí•œ íŒŒì¼ëª… ì—…ë°ì´íŠ¸

	if (file) {
		const reader = new FileReader();
		reader.onloadend = () => {
			setPreviewUrl(reader.result);
		};
		reader.readAsDataURL(file);
	}
  };

  return (
    <Wrapper>
      <Box
        sx={{
          width: 180,
          height: 180,
          borderRadius: "50%",
          margin: "0 auto",
          marginTop: 4,
          marginBottom: 2,
          backgroundImage: `url(${imageUrl})`,
          backgroundSize: "cover",
        }}
      />

      {/* íŒŒì¼ í•˜ë‚˜ë§Œ ì„ íƒí•˜ë„ë¡ í•  ê²ƒì´ê¸° ë•Œë¬¸ì— filesì˜ 0ë²ˆì§¸ ë°°ì—´ì„ ì§ì ‘ì ìœ¼ë¡œ ê°€ì ¸ì˜´ */}
      {/* input íƒœê·¸ ë‚´ì¥ ë§¤ì„œë“œ ì‚¬ìš©í•˜ì—¬ í™•ì¥ì ì œí•œ */}
      <input
        type="file"
        accept="image/jpg, image/jpeg, image/png"
        onChange={(e) => fileChange(e)}
        style={{ display: "none" }}
        ref={fileInputRef}
      />
      {/* ì‚¬ìš©ìê°€ ì„ íƒí•œ íŒŒì¼ëª… */}
      {selectedFileName && (
        <p style={{ fontSize: 12, textAlign: "center", margin: "0 auto" }}>
          {selectedFileName}
        </p>
      )}
      {/* ì»¤ìŠ¤í…€í•œ íŒŒì¼ ì—…ë¡œë” ë²„íŠ¼ */}
      <button onClick={fileUploadBtn} style={fileUploaderBtn}>
        {LABEL.BUTTONS.UPLOAD_IMAGE}
      </button>

      <Text
        id="standard-read-only-input"
        InputProps={{
          readOnly: true,
        }}
        variant="standard"
        placeholder={profile.login_id}
      />
      <Text
        id="standard-basic"
        variant="standard"
        defaultValue={profile.user_nickname}
        onChange={inputNickname}
        onBlur={nickNameValidate}
        helperText={nickNameRegMsg !== "" ? nickNameRegMsg : ""}
      />
      <Text
        id="standard-basic"
        variant="standard"
        placeholder={LABEL.INPUT.PLACE_HOLDER.CURRENT_PW}
        onChange={inputCurPw}
        type="password"
      />
      <Text
        id="standard-basic"
        variant="standard"
        placeholder={LABEL.INPUT.PLACE_HOLDER.NEW_PW}
        onChange={inputNewPw}
        type="password"
        onBlur={() => validation()}
        value={newPw}
        helperText={pwRegMsg}
      />
      <Text
        id="standard-basic"
        variant="standard"
        placeholder={LABEL.INPUT.PLACE_HOLDER.NEW_PW_REPEAT}
        type="password"
        onBlur={confirmNewPassword}
        helperText={confirmNewPwRegMsg !== "" ? confirmNewPwRegMsg : ""}
      />
      {profile.user_reg_dv === "W" && (
        <Text
          id="standard-basic"
          variant="standard"
          defaultValue={profile.author_info}
          onChange={inputInfo}
          // onBlur={nickNameValidate}
          // helperText={nickNameRegMsg !== "" ? nickNameRegMsg : ""}
        />
      )}
      {/* <button type="submit" onClick={onEditProfile}>
				{" "}
				ì™„ë£Œ{" "}
			</button> */}
      <ButtonBox>
        <Buttons
          type={CODE.BUTTON.BASIC}
          name={LABEL.BUTTONS.SUBMIT}
          onEditProfile={onEditProfile}
          margin={"10px 0 0 auto"}
        />
      </ButtonBox>
    </Wrapper>
  );
};
export default EditProfile;